import React, { useEffect } from "react";
import $ from "jquery";
import bonk from "../assets/sounds/bonk2.m4a";

function About(props) {
  useEffect(() => {
    let x = document.getElementsByClassName("white");
    for (let i = 0; i < x.length; i++) {
      x[i].addEventListener("click", () => playLine(i));
    }

    let y = document.getElementsByClassName("black");
    for (let i = 0; i < y.length; i++) {
      y[i].addEventListener("click", playBonk);
    }
    function playLine(num) {
      const aud = new Audio(require(`../assets/sounds/${num}.wav`));
      aud.play();
    }
    function playBonk() {
      const aud = new Audio(bonk);
      aud.play();
    }
    $.fn.isInViewport = function() {
      var elementTop = $(this).offset().top;
      var elementBottom = elementTop + $(this).outerHeight();

      var viewportTop = $(window).scrollTop();
      var viewportBottom = viewportTop + $(window).height();

      return elementBottom > viewportTop && elementTop < viewportBottom;
    };
    $(window).on("resize scroll", function() {
      if ($(".move1").isInViewport) {
        $(".move1").addClass("fall");
      }
      if ($(".move2").isInViewport) {
        $(".move2").addClass("fall");
      }
    });
  });

  return (
    <div className="container">
      <h1 className="move1">
        A Little About <span style={{ fontStyle: "italic" }}>Me</span>
      </h1>
      <img
        className="move2 image"
        src={require("../assets/me-min.png")}
        alt="Derek"
      />
      <div className="desc">
        <div className="innerDesc">
          <ul>
            <li>
              I'm a recent graduate from the{" "}
              <span className="em">University of Southern California </span>{" "}
              with a B.S. in
              <span className="em"> Computational Neuroscience</span>.{" "}
            </li>
            <li>
              I have experience working in both the front and backend and am
              adept at navigating across the stack. I'm able to deliver fully
              functional applications that have good UI/UX design in mind with
              scalable data systems.
            </li>
            <li>
              I am passionate about creating empathic, consumer-facing products
              that provide meaningful services. I'm always seeking to learn more
              about technology and how I can leverage it to create more
              worthwhile products.
            </li>
          </ul>
        </div>
        <div style={{ marginBottom: "15px" }}>
          Outside of developing, I'm an avid basketball fan, classical music
          consumer / practioner, Minesweeper fanatic, aspiring philosophy nut, 
          and crazed cat man!
        </div>
      </div>
      <div>
        <ul className="set">
          <li className="white b" id="play"></li>
          <li className="black as"></li>
          <li className="white a"></li>
          <li className="black gs"></li>
          <li className="white g"></li>
          <li className="black fs"></li>
          <li className="white f"></li>
          <li className="white e"></li>
          <li className="black ds"></li>
          <li className="white d"></li>
          <li className="black cs"></li>
          <li className="white c"></li>
        </ul>
      </div>
      <div className="bounce-2">
        <i className="fa fa-angle-double-up fa-4x"></i>
      </div>
      <p>
        <b>Play Me!</b>
      </p>
    </div>
  );
}

export default About;
